import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import TalkToUsNew from "../../components/common/talk-to-us-new";
import { Link } from "gatsby";
import MoveForward from "../../components/common/move-forward";
import Faq from "../../components/common/faq";
import NFTPlatforms from "./nft-platform";
import nft from "../../styles/assets/images/nft/nft.png";
import nft_system from "../../styles/assets/images/nft/nft_system.webp";
import CtaButton from "../../components/common/contactbutton";

const NFTScreen = (props) => {
  const { db } = props;

  const nftFaq = [
    {
      question: "Can I create my own NFT and sell it?",
      answer:
        "Yes, of course. It is very simple to create and sell your own NFTs.",
    },
    {
      question: "What is NFT?",
      answer:
        "NFT is a unique digital asset certifying ownership/authenticity on the blockchain.",
    },
    {
      question: "How long does it take to create an NFT?",
      answer:
        "Creating an NFT usually takes minutes to hours, depending on the asset's complexity and the chosen platform.",
    },
    {
      question: "How much does it cost to create an NFT?",
      answer:
        "NFT creation costs vary based on asset type, platform fees, and associated gas fees.",
    },
  ];

  return (
    <div className="dev_nft">
      <div className="dev_nft_hero_wrapper">
        <Container>
          <Row>
            <Col lg={12} className="col_flex">
              <div className="dev_nft_hero">
                <h1 className="dev_title">Leading NFT Development Company </h1>
                <p className="dev_text">
                  We are a trusted, leading NFT development company. Join us for
                  a more creative way of exchanging digital content.
                </p>
                <div className="decenter_hero_sec_btn">
                  <Link to="/contact-us" className="demo_btn">
                    View Live Demo
                  </Link>
                  <button type="submit" className="consult_btn">
                    <TalkToUsNew label="Free Consult" db={db} />
                  </button>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </div>
      <Container>
        <Row className="dev_nft_row">
          <Col lg={6}>
            <div className="dev_what_nft_lhs">
              <h1>What is NFT?</h1>
              <p>
                NFTs, or Non-Fungible Tokens, are unique digital assets
                representing ownership and authenticity of specific items using
                blockchain technology.{" "}
              </p>
              <p>
                Unlike traditional cryptocurrencies, NFTs are indivisible and
                often used to tokenize digital art, music, or other exclusive
                content, enabling creators to monetize their work in a secure
                and transparent way.
              </p>
              <div className="crypto_btns_grp">
                <Link className="prime_submit_btn" to="/contact-us">
                  View Live Demo
                </Link>
                <button className="second_consult_btn" type="button">
                  <TalkToUsNew label="Free Consult" db={db} />
                </button>
              </div>
            </div>
          </Col>
          <Col lg={6}>
            <div className="dev_what_nft_rhs">
              <img src={nft} alt="nft development" />
            </div>
          </Col>
        </Row>
      </Container>
      <CtaButton db={db} />
      <Container>
        <Row
          className="dev_nft_row dev_nft_sol_wrapper"
          data-aos="flip-down"
          data-aos-delay="500"
        >
          <Col md={12}>
            <div className="dev_nft_sol">
              <h1 className="text-center">NFT Development Services</h1>

              <p>
                Justtry Tech is a leading NFT development company that provides
                comprehensive technical support for developing NFTs and
                cutting-edge NFT Marketplaces in multiple industries such as
                music, arts, games, collectibles and etc.,
              </p>

              <p>
                Justtry Tech offers end-to-end NFT Development Services,
                covering everything from design to deployment. In addition to
                crafting non-fungible tokens and NFT Marketplaces, we also
                provide substantial assistance with NFT Storage Solutions. Our
                proficient NFT developers can assess your needs and determine
                the feasibility of launching your own NFT Marketplace on
                platforms like Polygon Matic, Tezos, Solana, Binance Smart
                Chain, Polkadot, and other potential blockchain platforms.
              </p>

              <div className="crypto_btns_grp">
                <Link className="prime_submit_btn" to="/contact-us">
                  View Live Demo
                </Link>
                <button className="second_consult_btn" type="button">
                  <TalkToUsNew label="Free Consult" db={db} />
                </button>
              </div>
            </div>
          </Col>
        </Row>
      </Container>
      <div className="dev_nft_service_wrapper">
        <Container>
          <Row className="dev_nft_row col_gap">
            <Col lg={12}>
              <h1 className="text-white">
                Our Prominent <br /> NFT Development Services
              </h1>
            </Col>
            <Col lg={4} md={6} sm={12} className="dev_nft_service_card">
              <div className="dev_nft_header">
                NFT Minting Platform Development
              </div>
              <div className="dev_nft_body">
                Create a seamless and customizable NFT minting platform that
                empowers creators to tokenize their digital assets with ease,
                facilitating the creation and launch of unique non-fungible
                tokens.
              </div>
            </Col>

            <Col lg={4} md={6} sm={12} className="dev_nft_service_card">
              <div className="dev_nft_header">
                NFT Token
                <br /> Development
              </div>
              <div className="dev_nft_body">
                Craft bespoke NFT tokens with advanced features, ensuring
                authenticity, scarcity, and interoperability, catering to the
                diverse needs of artists, developers, and businesses in the
                digital realm.
              </div>
            </Col>

            <Col lg={4} md={6} sm={12} className="dev_nft_service_card">
              <div className="dev_nft_header">
                NFT Marketplace
                <br /> Development
              </div>
              <div className="dev_nft_body">
                Build a dynamic NFT marketplace that provides a user-friendly
                interface for buyers and sellers to trade digital assets
                securely, fostering a vibrant ecosystem for unique tokenized
                creations.
              </div>
            </Col>

            <Col lg={4} md={6} sm={12} className="dev_nft_service_card">
              <div className="dev_nft_header">
                NFT Game <br />
                Development
              </div>
              <div className="dev_nft_body">
                Dive into the world of blockchain gaming by developing
                NFT-integrated games, allowing players to own, trade, and
                monetize in-game assets as unique tokens, creating a new era of
                immersive gaming experiences.
              </div>
            </Col>

            <Col lg={4} md={6} sm={12} className="dev_nft_service_card">
              <div className="dev_nft_header">
                NFT Staking <br />
                Platform Development
              </div>
              <div className="dev_nft_body">
                Develop a robust NFT staking platform enabling token holders to
                earn rewards by participating in the network, fostering
                community engagement and ensuring a decentralized and secure
                ecosystem.
              </div>
            </Col>

            <Col lg={4} md={6} sm={12} className="dev_nft_service_card">
              <div className="dev_nft_header">
                NFT Smart
                <br /> Contract Development
              </div>
              <div className="dev_nft_body">
                Engineer smart contracts tailored for NFTs, ensuring transparent
                and trustless transactions, automated royalty distributions, and
                customizable functionalities to meet the specific requirements
                of digital asset creators.
              </div>
            </Col>

            <Col
              lg={{ span: 4, offset: 2 }}
              md={6}
              sm={12}
              className="dev_nft_service_card"
            >
              <div className="dev_nft_header">
                NFT Exchange <br />
                Development
              </div>
              <div className="dev_nft_body">
                Construct a cutting-edge NFT exchange platform, providing a
                seamless marketplace for users to buy, sell, and trade
                non-fungible tokens, with features like liquidity pools and
                advanced trading options.
              </div>
            </Col>

            <Col lg={4} md={6} sm={12} className="dev_nft_service_card">
              <div className="dev_nft_header">
                NFT Storage <br />
                Solutions
              </div>
              <div className="dev_nft_body">
                Implement secure and scalable storage solutions for NFTs,
                utilizing decentralized and distributed technologies to ensure
                the safekeeping and accessibility of digital assets, enhancing
                the longevity and reliability of NFT storage.
              </div>
            </Col>
          </Row>
        </Container>
      </div>
      <NFTPlatforms />
      <Container>
        <Row className="dev_nft_row">
          <Col md={12}>
            <h1 className="text-center mb-4">
              What Do Our NFT Systems Support?
            </h1>
            <p className="text-center">
              We specialize in building robust NFT systems that support and
              optimize the four fundamental processes involved in NFT
              transactions.
            </p>
          </Col>
          <Col sm={12}>
            <Row>
              <Col
                lg={4}
                className="d-flex justify-content-center align-items-center justify-content-lg-end "
              >
                <Row className="nft_system_row">
                  <Col
                    md={12}
                    className="d-flex justify-content-center align-items-center justify-content-lg-end "
                  >
                    <div className="dev_nft_system_card">
                      <div className="dev_nft_system_title">NFT Creation</div>
                      <div className="dev_nft_system_content">
                        We mint diverse NFTs backed by unique assets.
                      </div>
                    </div>
                  </Col>
                  <Col
                    md={12}
                    className="d-flex justify-content-center align-items-center justify-content-lg-end "
                  >
                    <div className="dev_nft_system_card">
                      <div className="dev_nft_system_title">NFT Auction</div>
                      <div className="dev_nft_system_content">
                        Process for bids for NFTs in auctions.
                      </div>
                    </div>
                  </Col>
                </Row>
              </Col>
              <Col lg={4} className="col_flex">
                <div className="nft_system_img">
                  <img src={nft_system} alt="nft system" />
                </div>
              </Col>
              <Col lg={4} className="col_flex">
                <Row className="nft_system_row">
                  <Col
                    md={12}
                    className="d-flex justify-content-center align-items-center justify-content-lg-start "
                  >
                    <div className="dev_nft_system_card">
                      <div className="dev_nft_system_title">
                        NFT Transaction
                      </div>
                      <div className="dev_nft_system_content">
                        We ensure the security of your every NFT transactions.
                      </div>
                    </div>
                  </Col>
                  <Col
                    md={12}
                    className="d-flex justify-content-center align-items-center justify-content-lg-start "
                  >
                    <div className="dev_nft_system_card">
                      <div className="dev_nft_system_title">
                        Redeeming Of NFTs
                      </div>
                      <div className="dev_nft_system_content">
                        Our systems enable redeeming NFTs for physical or
                        digital assets.
                      </div>
                    </div>
                  </Col>
                </Row>
              </Col>
            </Row>
          </Col>
        </Row>
      </Container>

      <Faq faqData={nftFaq} />
      <MoveForward db={db} />
    </div>
  );
};

export default NFTScreen;
