import React from "react";
import Layout from "../components/layout";
import Seo from "../components/seo";
import BreadCrumb from "../components/common/breadcrumb";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import { graphql } from "gatsby";
import NFTDevIndex from "../screens/nft-development";

const NFTDevelopmentCompany = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "NFT Development Company", link: null },
  ];
  return (
    <Layout db={db}>
      <BreadCrumb items={breadcrumbLinks} />
      <main>
        <NFTDevIndex db={db} />
      </main>
    </Layout>
  );
};

export const Head = () => (
  <Seo
    title="NFT Development Company | Justtry Technologies"
    headTitle=" NFT Development Company | NFT Development Services"
    description="Our experienced team of NFT developers offers business-focused services in NFT token development across various use cases, including games, metaverse, real estate, and more."
    keywords="NFT Development, NFT Marketplace Development, NFT Development Services, NFT Gaming Platform Development, NFT Solutions, NFT Gaming Solutions"
  ></Seo>
);
export default NFTDevelopmentCompany;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
